@font-face{
    font-family: 'Canela-Medium';
    src: url('./fonts/Canela-Regular.woff') format('woff')
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: antiquewhite;
    max-width: 1200px;
      margin: 20px auto;
      padding: 0 20px;
      max-width: 1200px;
}

.main-container h1  {
    color:rgb(104, 98, 98);
    font-family: 'Canela-Medium'

}

.main-block-one{
display:grid;
grid-template-columns: 1fr 1fr;
}

form {
    display:flex;
    flex-direction:column;
    margin-top:7%;
    align-items:center;
    font-family: 'Roboto', sans-serif;
    color:rgb(104, 98, 98);
    border-left: 1px solid rgb(104, 98, 98);
}

form button {
background-color:rgb(139, 23, 23);
color:white;
border:none;
cursor:pointer;
border-radius:10%;
margin-top:1%;
}

form label{
    margin: 1%;
}

form input {
    border:1px solid rgb(104, 98, 98);
    border-radius:5%;
}

.main-subheading {
    font-family: 'Canela-Medium';
   
}

.cafe-container-container {
    margin-top:5%;
    color:rgb(104, 98, 98);
}

.cafe-container{
    padding: 1%;
    margin:1%;
    transform:translateX(-2%);
    letter-spacing:1px;
}

.cafe-container:hover{
    background-color:rgb(104, 98, 98);
    color:antiquewhite;
    border-radius: 5px;
}

.cafe-grid{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows:1fr 1fr 1fr;
}
.cafe-container img {
    width:150px;
    height:100px;
    border-radius:5%;
}

.cafe-header {
 text-align:center;
 margin-top:5%;
 color:rgb(104, 98, 98);
 font-family: 'Canela-Medium'
 
}

.cafe-header img {
    height:300px;
    width:300px;
    border-radius:100%;
    margin:1%;
}

.contact-details {
    display:flex;
    flex-direction: row;
    justify-content: center;
    list-style-type: none;
}

.contact-details li {
    padding:1%;
}

.reviews-container {
    background-color:rgb(255, 255, 255);
    padding:3%;
    margin-top:3%;
    border-radius:10px;
}

.review-container {
    border-bottom: 1px solid rgb(153, 151, 151);
    padding:2%;
    color:rgb(85, 81, 81);
}

.review-container button {
    font-family: 'Roboto', sans-serif;
    color:white;
    background-color:rgb(85, 81, 81);
    border:hidden;
}

.nav{
border-bottom: 1px solid rgb(150, 144, 144);
}

.submit-message {
    margin-top:2%;
}

.submit-message button {
    background-color:antiquewhite;
    color:black;
    transform:translateX(20%);
    border:1px solid rgb(150, 144, 144);
}

h3:hover {
    background-color: rgb(85, 81, 81);
    color:antiquewhite;
    width:5%;
    border-radius:5px;
}